



























import Vue, { PropType } from 'vue';
import CheckIcon from '@/assets/check.svg';
import MinusIcon from '@/assets/minus.svg';
import { CheckboxValue, CheckboxChecked } from '@/models/checkbox';

export default Vue.extend({
  name: 'ACheckbox',
  components: {
    CheckIcon,
    MinusIcon,
  },
  inheritAttrs: false,
  model: {
    prop: 'checked',
    event: 'change',
  },
  props: {
    checked: {
      type: [Boolean, Array] as PropType<CheckboxChecked>,
      default: false,
    },
    // eslint-disable-next-line vue/require-default-prop
    value: null as CheckboxValue,
    label: { type: String, default: '' },
    disabled: Boolean,
    indeterminate: Boolean,
  },
  computed: {
    localChecked: {
      get(): CheckboxChecked {
        return this.checked;
      },
      set(checked: CheckboxChecked) {
        this.$emit('change', checked);
      },
    },
    localIndeterminate: {
      get(): boolean {
        return this.indeterminate;
      },
      set(indeterminate: boolean) {
        this.$emit('update:indeterminate', indeterminate);
      },
    },
  },
  watch: {
    localChecked(): void {
      if (this.localIndeterminate) {
        this.localIndeterminate = false;
      }
    },
  },
});
